import { Component } from "@angular/core";
import {
  IonicPage,
  NavController,
  NavParams,
  ViewController,
  ModalController
} from "ionic-angular";
import { ProgramCheckPKViewModel } from "../../Model/ViewModel/ProgramCheckPKViewModel";
import { TableViewModel } from "../../Model/ViewModel/TableViewModel";

/**
 * Generated class for the programModalPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: "page-batch-program-check-pk-edit-modal",
  templateUrl: "batch-program-check-pk-edit-modal.html"
})
export class BatchProgramCheckPkEditModalPage {
  item: ProgramCheckPKViewModel;
  temp_table_item:TableViewModel;
  check_table_item:TableViewModel;
  mode: string;
  CanEditBatch: boolean;
  title: string;
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController,
    public modalCtrl: ModalController
  ) {
    this.title = "批次檢核PK對應";


    this.item = new ProgramCheckPKViewModel();
    this.item.program_no=navParams.data.item.program_no;
this.item.column_id=navParams.data.item.column_id;
this.item.check_pk_column=navParams.data.item.check_pk_column;
this.item.temp_pk_column=navParams.data.item.temp_pk_column;
this.item.create_time=navParams.data.item.create_time;
this.item.creator=navParams.data.item.creator;
this.item.last_update_time=navParams.data.item.last_update_time;
this.item.modifier=navParams.data.item.modifier;
this.temp_table_item=navParams.data.temp_table_item;

    this.CanEditBatch = navParams.data.CanEditBatch;
    this.mode = navParams.data.mode;

    console.log(this.CanEditBatch);
  }


  SelectSrcColumn() {

    let modal = this.modalCtrl.create("TableDetailSelectModalPage", {
      table_item:this.temp_table_item,
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;
      this.item.temp_pk_column = select_data.columnname;
    });

    modal.present();
  }

  Save() {

    this.viewCtrl.dismiss(this.item);
  }
  close() {
    this.viewCtrl.dismiss();
  }
  ionViewDidLoad() {
    console.log("ionViewDidLoad ProgramModalPage");
  }
}



